import React, { useState } from 'react';
import {episodeNumber} from '../App';
import {Swiper, SwiperSlide} from 'swiper/react';
import 'swiper/swiper.min.css';
import 'swiper/css/navigation'



function Player({video, choose, setChoose, close, setClose}) {

  const [season, setSeason] = useState(0);
  
  

  //here we are destruturing to get indvidual arrays of the object found in the video array. However didnt work
  //const [seasonOne, seasonTwo, seasonThree] = video
  let newArray = []
  //so this what to do when you have that shit
  video.map((sth) => Object.values(sth).map((item) => newArray.push(item)))

//this was my attempt to write logic to render certain elements
    // console.log(newArray[0])
    // let temp = [];
    // for (const episode in newArray){
    // newArray[episode].map((ep) => temp.push(ep.episode_no))
    // }
    // console.log(temp)
    // if (temp.length % 2 != 0){
    //   setExtra(true)
    // }
    
  

  return (
    <div>
    {close ? <div className='player'>
      <button onClick={() => setClose(false)} className='close-btn'>x</button>
      <div className='videoPlayer'>
        <h1>H-Player</h1>
        {newArray[season].filter((vid) => vid.episode_no === choose).map((fil) => (
        <div key={fil.episode_no}>
        <iframe allowFullScreen='true' className='iframePlayer' src={fil.episode_url} sandbox='allow-scripts' />
        <h3>{fil.show_title}{fil.season}{fil.episode_no}</h3>
        </div>
        ))
        }
      </div>
      <button onClick={() => setSeason(0)} className='button'>season one</button>
      <button onClick={() => setSeason(1)} className='button'>season two</button>
      <button onClick={() => setSeason(2)} className='button'>season three</button>
      <div className='selectors'>
      <Swiper
        grabCursor={true}
        slidesPerView={3}
        spaceBetween={10}
        navigation
      >
      <SwiperSlide>
        <button className='button' onClick={() => setChoose('1')}>Episode one</button>
      </SwiperSlide>
      <SwiperSlide>
        <button className='button' onClick={() => setChoose('2')}>Episode two</button>
      </SwiperSlide>
      <SwiperSlide>
        <button className='button' onClick={() => setChoose('3')}>Episode three</button>
      </SwiperSlide>
      <SwiperSlide>
        <button className='button' onClick={() => setChoose('4')}>Episode four</button>
      </SwiperSlide>
      <SwiperSlide>
        <button className='button' onClick={() => setChoose('5')}>Episode five</button>
      </SwiperSlide>
      <SwiperSlide>
        <button className='button' onClick={() => setChoose('6')}>Episode six</button>
      </SwiperSlide>
      <SwiperSlide>
        <button className='button' onClick={() => setChoose('7')}>Episode seven</button>
      </SwiperSlide>
      <SwiperSlide>
        <button className='button' onClick={() => setChoose('8')}>Episode eight</button>
      </SwiperSlide>
      <SwiperSlide>
        <button className='button' onClick={() => setChoose('9')}>Episode nine</button>
      </SwiperSlide>
      <SwiperSlide>
        <button className='button' onClick={() => setChoose('10')}>Episode ten</button>
      </SwiperSlide>
      <SwiperSlide>
        <button className='button' onClick={() => setChoose('11')}>Episode 11</button>
      </SwiperSlide>
      <SwiperSlide>
        <button className='button' onClick={() => setChoose('12')}>Episode 12</button>
      </SwiperSlide>
      <SwiperSlide>
        <button className='button' onClick={() => setChoose('13')}>Episode 13</button>
      </SwiperSlide>
        </Swiper>
     
        </div> 
      </div> : null }
    </div>
  )
      }


export default Player
