import {React, useState, useEffect} from 'react';
import ReactPlayer from 'react-player';
import Player from './components/Player';
import Landing from './components/Landing'


function App() {

  const [video, setVideo] = useState([]);
  const [choose, setChoose] = useState('1')
  const [series, setSeries] = useState('files/episodes.json')
  const [close,setClose] = useState(true)

  useEffect(() => {
    fetchAPI(series);
  },[series])



  const fetchAPI = (api) => {
    // const api = '/express_backend'
    // const api = 'files/episodes.json'
    switch(api){
      case 'files/episodes.json':
        fetch(api)
        .then((response) => {
          return response.json() ;
        })
        .then((API) => {
          console.log(API)
          setVideo(API)
        })
        break;
      case 'files/bandOfBrothers.json':
        fetch(api)
        .then((response) => {
          return response.json() ;
        })
        .then((API) => {
          console.log(API)
          setVideo(API)
        })
        break;
      case 'files/justified.json':
        fetch(api)
        .then((response) => {
          return response.json() ;
        })
        .then((API) => {
          console.log(API)
          setVideo(API)
        })
        break;
        case 'files/madMen.json':
          fetch(api)
          .then((response) => {
            return response.json() ;
          })
          .then((API) => {
            console.log(API)
            setVideo(API)
          })
          break;
      default:
        fetch('files/episodes.json')
        .then((response) => {
          return response.json() ;
        })
        .then((API) => {
          console.log(API)
          setVideo(API)
        })
        break;
    }
  }




  return (
    <div>
      <Landing setSeries={setSeries} setClose={setClose}/>
      <Player video={video} setVideo={setVideo} choose={choose} setChoose={setChoose} close={close} setClose={setClose}/>

    </div>
  )
}



export default App