import React from 'react'

function Landing({setSeries, setClose}) {
  return (
    <div className='gridGallery'>
      <img onClick={() => {setSeries('files/episodes.json'); setClose(true)}} className='grid-item01 grid-item' src='images/betterCallSaul.jpg' />
      <img onClick={() => {setSeries('files/bandOfBrothers.json'); setClose(true)}} className='grid-item02 grid-item' src='images/bandOfBrothers.jpg' />
      <img onClick={() => {setSeries('files/justified.json'); setClose(true)}} className='grid-item03 grid-item' src='images/justified.jpg' />
      <img onClick={() => {setSeries('files/madMen.json'); setClose(true)}} className='grid-item04 grid-item' src='images/madMen.jpg' />
      <h3 className='grid-item05 grid-header'>Better Call Saul</h3>
      <h3 className='grid-item06 grid-header'>Band of Brothers</h3>
      <h3 className='grid-item07 grid-header'>Justified</h3>
      <h3 className='grid-item08 grid-header'>Mad Men</h3>
    </div>
  )
}

export default Landing